.tag {
    background: black;
    color: #fff;
    width: 115px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Inria Serif;
    font-weight: 600;
    text-align: center;
    font-size: 1rem;
    padding: 5px;
    height: 20px;
    margin-right: 10px;
    margin-top: 1vh;
}


@media screen and (max-width:740px) {

    .tag {
        font-size: 2.5vw;
        width: 20vw;
        margin-top: 0;
    }
}