div p {
    display: flex;
    width: 44.125rem;
    justify-content: center;
    align-items: center;
    margin: auto;

}

div text {

    width: 44.125rem;
    height: 14.25rem;
    flex-shrink: 0;

}
.paragraphe-comp {
	display: grid;
	grid-auto-columns: min-content;
	margin: auto;
}

