#button {
    display: flex;
    justify-content: flex-end;
    /* margin-right: 26rem; */
    text-decoration: none;

}

div .button-paragraphe {

    /* Button background */

    width: 6.2rem;
    height: 1.8rem;
    flex-shrink: 0;
    background-color: black;
    border-bottom: none;
    /* text */

    color: #FEFEFE;
    text-align: center;
    font-family: Inria Serif;
    font-size: 1rem;
    padding-top: 0.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-decoration: none;

}

div .button-paragraphe:hover {
    border-bottom: none;

    background-color: #0077B5;

    color: #FEFEFE;

    transition: 0.3s all;
}