div p {
    display: flex;
    width: 44.125rem;
    /* height: 14.25rem; */
    justify-content: center;
    align-items: center;
    margin: auto;

}


#school {
    width: 70rem;
}

.paragraphe {
    color: #000;
    text-align: justify;
    font-family: Inria Serif;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

#button {
    display: flex;
    justify-content: flex-end;
    /* margin-right: 26rem; */
    text-decoration: none;

}

div .button_paragraphe {

    /* Button background */

    width: 6.2rem;
    height: 1.8rem;
    flex-shrink: 0;
    background-color: black;
    border-bottom: none;
    /* text */

    color: #FEFEFE;
    text-align: center;
    font-family: Inria Serif;
    font-size: 1rem;
    padding-top: 0.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-decoration: none;

}

div .button_paragraphe:hover {
    border-bottom: none;
    background-color: #7451eb;
    transition: 0.3s all;
}

@media (max-width: 820px) {
    .paragraphe,
    #school{
        font-size: 1rem;
        width: 88vw;

    }

 

}